<template>
  <div id="app">

    <div class="progress-container">
      <div class="progress-bar" :style="{ width: progress + '%' }"></div>
    </div>

    <div class="layout-container">

      <div class="sidebar">
        <Header />
        <Nav />
      </div>
      
      <header class="mobile-header">
        <Header />
      </header>

      <main class="content">
        <div class="content-wrapper">  
          <router-view />
        </div>
      </main>
      
      <nav class="mobile-nav">
        <Nav />
      </nav>
      <footer class="footer">
        <Footer />
      </footer>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue'
import Header from './components/Header.vue'
import Nav from './components/Nav.vue'
import Footer from './components/Footer.vue'

export default {
  name: 'App',
  components: {
    Header,
    Nav,
    Footer,
  },
  setup() {
    const progress = ref(0)

    const updateProgress = () => {
      const scrollTop = window.scrollY
      const docHeight = document.documentElement.scrollHeight - window.innerHeight
      const scrolled = (scrollTop / docHeight) * 100
      progress.value = Math.min(100, Math.max(0, scrolled))
    }

    onMounted(() => {
      window.addEventListener('scroll', updateProgress)
      updateProgress()
    })

    onUnmounted(() => {
      window.removeEventListener('scroll', updateProgress)
    })

    return { progress }
  }
}

</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
}

.layout-container {
  display: grid;
  grid-template-areas:
    "sidebar content"
    "footer footer";
    grid-template-columns: 200px 1fr;
  margin-left: 3rem;
  gap: 8rem;
  padding:1rem;
  padding-right: 7rem;

}

.sidebar {
  grid-area: sidebar;
  width: 200px; 
  border-left: 60px;
  position: fixed;
  top: 80px;
  
}

.content-wrapper {
  max-width: 1200px; 
  width: 100%;
  margin: 0 auto;
}

.content {
  background-color:rgba(143, 143, 135, 0.075);
  border-radius: 3px;
  grid-area: content;
  flex: 1;
  padding: 1rem;
  min-width: 0;
  max-width:calc(100%-200px);
}

.footer{
  grid-area: footer;
  padding: 1rem;
  text-align:center;
  align-items: center;
  justify-content: center;
  grid-column: 1 / -1; 
}

.progress-container {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background: transparent;
      z-index: 1000;
    }
    
    .progress-bar {
      height: 100%;
      background: linear-gradient(90deg, #c5eddd, #9dc3f7);
      width: 0%;
      transition: width 0.2s ease;
    }

.mobile-header,
.mobile-nav {
  display: none; 
}

@media (max-width: 768px) {
  .layout-container {
    display: flex;
    flex-direction: column;
    gap:1rem;
  }

  .sidebar{
    display: none;
  }
   .mobile-header {
    display: block;
    order:1;
    text-align: left;
    width:fit-content
  }
  
  .mobile-nav {
    display: block;
    order: 2; 
  }
  
  .content {
    order: 3;
  }
}

Footer {
  order:4;
  margin-top: auto;
}
</style>
